import React, { Dispatch } from "react";
import clsx from "clsx";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import MiniDrawer from "./MiniDrawer";
import LinkConst from "./LinkConst";
import MobileMenu from "./MobileMenu";
import MenuRender from "./MenuRender";
import IconController from "./LeftIcons";
import { HeaderState, HeaderAction, IHeaderProps } from "../Header";
import SearchInput from "./SearchInput";

const drawerWidth = 240;
const version = process.env.REACT_APP_VERSION;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    hide: {
      display: "none",
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
  })
);

export type AppBarInterface = AppBarProps & IHeaderProps;

interface AppBarProps {
  state: HeaderState;
  dispatch: Dispatch<HeaderAction>;
}
const MainAppBar = (props: AppBarProps & IHeaderProps) => {
  const classes = useStyles();

  const { menuIsOpen } = props.state;

  const handleDrawerOpen = () => {
    props.dispatch({ type: "menu-toggle" });
  };

  return (
    <AppBar
      color={"default"}
      position="relative"
      className={clsx(classes.appBar, {
        [classes.appBarShift]: menuIsOpen,
      })}
    >
      <Toolbar id="back-to-top-anchor">
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={handleDrawerOpen}
          edge="start"
          className={clsx(classes.menuButton, {
            [classes.hide]: menuIsOpen,
          })}
        >
          <MenuIcon />
        </IconButton>
        <Link component={RouterLink} color="secondary" to="/">
          
        </Link>
        <p onClick={() => sessionStorage.clear()}>({version})</p>
        <LinkConst openStatus={menuIsOpen} />
        <SearchInput />
        <IconController {...props} />
      </Toolbar>
      <MiniDrawer {...props} />
      <MobileMenu {...props} />
      <MenuRender {...props} />
    </AppBar>
  );
};
export default MainAppBar;
