import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import {
  CircularProgress,
  Grid,
  createStyles,
  Theme,
  Typography,
  WithStyles,
  withStyles,
  CardMedia,
} from "@material-ui/core";

import { AppActions, AppState } from "../../../_types";
import { ICourse } from "../../../_types/course.types";
import {
  PathFinder,
  pathFinder,
  thumbnailsFinder,
} from "../../../_helpers/constants";
import { requestCourse } from "../../../_actions/course.actions";
import { CourseItem } from "./CourseItems";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "99vw",
      minHeight: "calc(100vh - 128px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 112px)",
        padding: theme.spacing(1),
      },
      padding: theme.spacing(2),
      display: "flex",
      flexDirection: "column",
      justifyContent: "flex-start",
    },
    autoMargin: {
      margin: "auto",
    },
    content: {
      position: "relative",
      minHeight: "calc(100vh - 160px)",
      [theme.breakpoints.down("sm")]: {
        minHeight: "100%",
      },
      border: `1px solid ${theme.palette.secondary.main}`,
      height: "100%",
      width: "100%",
    },
    text: {
      padding: 20,
    },
    image: {
      width: "100%",
      height: "100%",
      opacity: "0.3",
      position: "absolute",
    },
  });

interface CourseProps extends WithStyles<typeof useStyles> {
  data?: ICourse;
  loading: boolean;
  error: string;
  pathname: string;
}

type Props = CourseProps & LinkDispatchProps;

class Course extends Component<Props> {
  componentDidMount() {
    const path = pathFinder(this.props.pathname);
    this.props.requestCourse(path);
  }

  render() {
    const { classes, data, loading, error, pathname } = this.props;
    return (
      <Grid container justify="center" className={classes.root}>
        {loading ? (
          <CircularProgress color="secondary" className={classes.autoMargin} />
        ) : data ? (
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} xl={6} xs={12}>
              <div className={classes.content}>
                <CardMedia
                  image={thumbnailsFinder(data.thumbnails)}
                  className={classes.image}
                />
                <Typography
                  gutterBottom
                  variant="body1"
                  component="h6"
                  className={classes.text}
                >
                  {data.description}
                </Typography>
              </div>
            </Grid>
            <Grid item lg={6} md={6} xl={6} xs={12}>
              <Grid container justify="flex-end">
                {data?.course_select.map((course, k) => (
                  <CourseItem
                    key={k}
                    type={course.info.genre.name}
                    path={pathname}
                    courseItem={course}
                  />
                ))}
              </Grid>
            </Grid>
          </Grid>
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </Grid>
    );
  }
}

interface LinkDispatchProps {
  requestCourse: (path: PathFinder) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): CourseProps => ({
  classes: ownProps.classes,
  pathname: state.router.location.pathname,
  ...state.single.course,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestCourse: bindActionCreators(requestCourse, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Course));
