import { Dispatch } from "redux";
import {
  REQUEST_POSTS,
  SUCCESS_POSTS,
  FAILURE_POSTS,
  REQUEST_POST,
  SUCCESS_POST,
  FAILURE_POST,
} from "../_types/post.types";
import { AppState } from "../_types";
import { AppActions } from "../_types";
import { getTheTime } from "../_helpers/constants";
import { errorHandler } from "../_helpers/alert";
import { API } from "../_helpers/api";

export const requestAllPost = () => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const postState = getState().bank.posts;
    const currentTime = getTheTime();
    const resetTime = currentTime - postState.nextLoad;
    if (postState.data && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_POSTS });

    API.get(`/post/v2`)
      .then(res => dispatch({
        type: SUCCESS_POSTS, payload: {
          data: res.data,
          nextLoad: getTheTime(10)
        }
      }))
      .catch(err => errorHandler(err, FAILURE_POSTS))
  }
)

export const requestPost = (id: number) => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const postState = getState().bank.posts.data;

    const found = postState?.find(post => post.id === id);
    if (found) {
      dispatch({ type: SUCCESS_POST, data: found })
    } else {
      dispatch({ type: REQUEST_POST });
      API.get(`/post/v2?id=${id}`)
        .then((res) => dispatch({ type: SUCCESS_POST, data: res.data[0] }))
        .catch(err => errorHandler(err, FAILURE_POST))
    }
  }
)