import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  CardMedia,
  Typography,
  CardContent,
  CardActionArea,
  Chip,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../../_store/store.config";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { ILesson } from "../../../_types/lesson.types";
import { ICourse } from "../../../_types/course.types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      maxWidth: 285,
      maxHeight: 260,
      margin: theme.spacing(1),
    },
    cardAction: {
      justifyContent: "space-between",
      margin: "inherit",
    },
    content: {
      minHeight: 130,
    },
    rightChip: {
      position: "absolute",
      cursor: "pointer",
      margin: theme.spacing(1),
      backgroundColor: "green",
    },
    leftChip: {
      position: "absolute",
      right: 0,
      cursor: "pointer",
      margin: theme.spacing(1),
    },
  })
);

type Props = {
  courseItem: ICourse;
  index: number;
  type: string;
  path: string;
};

export const CourseItem: React.FC<Props> = ({
  index,
  type,
  courseItem,
  path,
}) => {
  const classes = useStyles();
  const { title, id, thumbnails, grade } = courseItem;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    store.dispatch(push(path + "/" + type + id));
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={clickHandler}>
        <Chip label={index + 1} className={classes.rightChip} />
        <Chip label={grade} className={classes.leftChip} color="secondary" />
        <CardMedia
          component="img"
          alt={title}
          height="130"
          image={thumbnailsFinder(thumbnails)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="body1" component="h6">
            {title}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

type ExerciseProps = {
  exerciseItem: ILesson;
  index: number;
  type: string;
  path: string;
};

export const ExerciseItem: React.FC<ExerciseProps> = ({
  exerciseItem,
  index,
  type,
  path,
}) => {
  const classes = useStyles();
  const { title, id, featured_img, info } = exerciseItem;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    store.dispatch(push(path + "/" + type + id));
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={clickHandler}>
        <Chip label={index + 1} className={classes.rightChip} />
        <Chip
          label={info.grade.name}
          className={classes.leftChip}
          color="secondary"
        />
        <CardMedia
          component="img"
          alt={title}
          height="130"
          image={thumbnailsFinder(featured_img)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="body1" component="h6">
            {title}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};
