import {
  LOGIN_FAILURE,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  USER_LOGOUT,
  REGISTER_FAILURE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
  LOST_REQUEST,
  LOST_SUCCESS,
  LOST_FAILURE,
  IRegister,
  IUser,
  ILost
} from "../_types/user.types";
import { Dispatch } from "react";
import { AppActions } from "../_types";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { errorHandler, snackBarMaker } from "../_helpers/alert";
import { go, goBack, push } from 'connected-react-router'
import { API } from "../_helpers/api";

export const loginRequest = (user: IUser) => (
  (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: LOGIN_REQUEST });
    API.post(`/jwt-auth/v1/token`, user)
      .then(res => {
        dispatch({ type: LOGIN_SUCCESS, user: res.data });
        localStorage.setItem("user", JSON.stringify(res.data));
        API.defaults.headers.common = { Authorization: "Bearer " + res.data.token };
        dispatch(goBack());
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(
            `${res.data.user_display_name} logged in successfully.`,
            "success"
          ),
        });
      })
      .catch(err => errorHandler(err, LOGIN_FAILURE))
  }
)

export const logoutRequest = () => (
  (dispatch: Dispatch<AppActions>) => {
    localStorage.removeItem("user");
    API.defaults.headers.common = {};
    dispatch(go(0));
    dispatch({ type: USER_LOGOUT });
  }
)

export const registerRequest = (user: IRegister) => (
  (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: REGISTER_REQUEST });
    API.post(`/jwt-auth/v1/register`, user)
      .then(
        (res) => {
          dispatch({ type: REGISTER_SUCCESS, user: res.data });
          dispatch(push("/login"));
          dispatch({
            type: SHOW_SNACKBAR,
            snackbar: snackBarMaker(res.data.message, "success"),
          });
        })
      .catch(err => errorHandler(err, REGISTER_FAILURE))
  }
)

export const lostRequest = (user: ILost) => (
  (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: LOST_REQUEST });

    API.post(`/jwt-auth/v1/lostpassword`, user)
      .then(
        (res) => {
          const message: string = res.data.message.toString();
          dispatch({ type: LOST_SUCCESS, message });
          dispatch(push("/login"));
          dispatch({
            type: SHOW_SNACKBAR,
            snackbar: snackBarMaker(message, "success"),
          });
        })
      .catch(err => {
        errorHandler(err, LOST_FAILURE)
      })
  }
)
