import React from "react";
import * as Yup from "yup";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { Field, Form, Formik } from "formik";
import { TextField } from "formik-material-ui";
import { Link as RouterLink } from "react-router-dom";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import {
  Avatar,
  CssBaseline,
  Box,
  Button,
  Checkbox,
  Container,
  FormHelperText,
  Link,
  Typography,
  LinearProgress,
} from "@material-ui/core";

import { AppState } from "../../_types";
import { AppActions } from "../../_types";
import { IRegister } from "../../_types/user.types";
import { Captcha } from "../../assets/captcha/Captcha";
import { registerRequest } from "../../_actions/user.actions";
import { push } from "connected-react-router";
import { Path } from "history";

interface RegisterPageProps {
  loading: boolean;
  loggedIn: boolean;
}

type Props = RegisterPageProps & LinkDispatchProps;

class RegisterPage extends React.Component<Props> {
  componentDidMount() {
    if (this.props.loggedIn) {
      this.props.push("/");
    }
  }
  render() {
    const { loading, loggedIn } = this.props;
    return !loggedIn ? (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign Up
          </Typography>
          <Formik
            initialValues={{
              email: "",
              firstname: "",
              lastname: "",
              username: "",
              password: "",
              confirm_password: "",
              recaptcha: false,
              policy: false,
            }}
            validationSchema={Yup.object().shape({
              email: Yup.string()
                .email("Must be a valid email")
                .max(255)
                .required("Email is required"),
              firstname: Yup.string()
                .max(255)
                .required("First name is required"),
              lastname: Yup.string().max(255).required("Last name is required"),
              username: Yup.string().max(255).required("User name is required"),
              password: Yup.string().max(255).required("Password is required"),
              confirm_password: Yup.string().oneOf(
                [Yup.ref("password"), ""],
                "Both password need to be the same"
              ),
              policy: Yup.boolean().oneOf([true], "This field must be checked"),
              recaptcha: Yup.boolean().oneOf(
                [true],
                "This field must be checked"
              ),
            })}
            onSubmit={(e) => {
              if (
                e.firstname &&
                e.lastname &&
                e.username &&
                e.email &&
                e.password
              ) {
                this.props.registerRequest({
                  firstname: e.firstname,
                  lastname: e.lastname,
                  username: e.username,
                  password: e.password,
                  email: e.email,
                });
              }
            }}
          >
            {({ handleChange, values, touched, errors }) => (
              <Form>
                <Box mb={3}>
                  <Typography
                    color="textSecondary"
                    align="center"
                    gutterBottom
                    variant="body2"
                  >
                    Use your email to create new account
                  </Typography>
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=""
                    margin="normal"
                    name="firstname"
                    variant="outlined"
                  />
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=" "
                    margin="normal"
                    name="lastname"
                    variant="outlined"
                  />
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=" "
                    margin="normal"
                    name="username"
                    variant="outlined"
                  />
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=" "
                    margin="normal"
                    name="email"
                    dir="ltr"
                    type="email"
                    variant="outlined"
                  />
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=""
                    margin="normal"
                    name="password"
                    type="password"
                    variant="outlined"
                  />
                  <Field
                    component={TextField}
                    color="secondary"
                    fullWidth
                    label=" "
                    margin="normal"
                    name="confirm_password"
                    type="password"
                    variant="outlined"
                  />
                  <Box alignItems="center" display="flex" ml={-1}>
                    <Checkbox
                      checked={values.policy}
                      name="policy"
                      onChange={handleChange}
                    />
                    <Typography color="textSecondary" variant="body1">
                      I have read the{" "}
                      <Link
                        color="secondary"
                        component={RouterLink}
                        to="#"
                        underline="always"
                        variant="body1"
                      >
                        Terms and Conditions{" "}
                      </Link>
                    </Typography>
                  </Box>
                  {Boolean(touched.policy && errors.policy) && (
                    <FormHelperText error>{errors.policy}</FormHelperText>
                  )}
                  <Box my={1}>
                    <Field
                      component={Captcha}
                      color="secondary"
                      fullWidth
                      label="Recaptcha"
                      margin="normal"
                      name="recaptcha"
                      type="recaptcha"
                      variant="outlined"
                    />
                  </Box>
                  {loading && <LinearProgress color="secondary" />}
                  <Box my={2}>
                    <Button
                      color="secondary"
                      disabled={loading}
                      fullWidth
                      size="large"
                      type="submit"
                      variant="contained"
                    >
                      Sign up now
                    </Button>
                  </Box>
                  <Typography
                    align="center"
                    color="textSecondary"
                    variant="body2"
                  >
                    Have an account?
                    <Link
                      color="secondary"
                      component={RouterLink}
                      to="/login"
                      variant="body2"
                    >
                      Sign in
                    </Link>
                  </Typography>
                </Box>
              </Form>
            )}
          </Formik>
        </div>
      </Container>
    ) : null;
  }
}

interface LinkDispatchProps {
  registerRequest: (user: IRegister) => void;
  push: (path: Path) => void;
}

const mapStateToProps = (state: AppState): RegisterPageProps => {
  const { loggedIn, loading } = state.auth;
  return {
    loggedIn,
    loading,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  registerRequest: bindActionCreators(registerRequest, dispatch),
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);
