import React, { useState } from "react";
import { ConnectedRouter } from "connected-react-router";
import { SnackbarProvider } from "notistack";
import jssRTL from "jss-rtl";
import { create } from "jss";
import { History } from "history";
import AppRouter from "./router";
import "./App.css";
import {
  Box,
  createStyles,
  jssPreset,
  makeStyles,
  responsiveFontSizes,
  StylesProvider,
  Theme,
  ThemeProvider,
} from "@material-ui/core";
import { LightTheme, DarkTheme } from "./components/Layout/theme";
import Header from "./components/Layout/Header";
import Notifier from "./components/Layout/Notifier";
import { localTheme } from "./_helpers/constants";
import Breadcrumb from "./components/Layout/Breadcrumbs";

const jss = create({ plugins: [...jssPreset().plugins, jssRTL()] });

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    app: {
      width: "100vw",
      height: "100vh",
      overflowX: "hidden",
      display: "flex",
      flexDirection: "column",
      overflow: "hidden",
    },
    mainBody: {
      height: "inherit",
      overflowX: "hidden",
      overflowY: "auto",
    },
  })
);

interface AppProps {
  history: History;
}

const App = ({ history }: AppProps) => {
  const [isDark, setIsDark] = useState<boolean>(localTheme("get"));
  const classes = useStyles();

  const theme = responsiveFontSizes(isDark ? DarkTheme : LightTheme);
  return (
    <ConnectedRouter history={history}>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={theme}>
          <SnackbarProvider>
            <Box className={classes.app}>
              <Header theme={{ isDark, setIsDark }} />
              <Notifier />
              <Breadcrumb />
              <Box id="main-body" className={classes.mainBody}>
                <AppRouter />
              </Box>
            </Box>
          </SnackbarProvider>
        </ThemeProvider>
      </StylesProvider>
    </ConnectedRouter>
  );
};
export default App;
