import {
  SAVE_GUIDES,
  REQUEST_GUIDE,
  SUCCESS_GUIDE,
  FAILURE_GUIDE,
  REQUEST_GUIDES,
  SUCCESS_GUIDES,
  FAILURE_GUIDES,
  GuideActionTypes,
  DefaultGuideState,
  DefaultGuidesState,
} from "../_types/guide.types";

const guidesReducerDefaultState: DefaultGuidesState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const guidesReducer = (
  state = guidesReducerDefaultState,
  action: GuideActionTypes
): DefaultGuidesState => {
  switch (action.type) {
    case REQUEST_GUIDES:
      return state;
    case SUCCESS_GUIDES:
      return {
        ...state,
        data: action.payload.data,
        nextLoad: action.payload.nextLoad,
        loading: false,
      };
    case SAVE_GUIDES:
      let match = false;
      if (state.data.length)
        match = action.data.every(item => state.data.map(_ => _.id === item.id));
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE_GUIDES:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const guideReducerDefaultState: DefaultGuideState = {
  loading: true,
  error: "Not Found",
};

export const guideReducer = (
  state = guideReducerDefaultState,
  action: GuideActionTypes
): DefaultGuideState => {
  switch (action.type) {
    case REQUEST_GUIDE:
      return { ...state, loading: true, data: undefined };
    case SUCCESS_GUIDE:
      return {
        ...state,
        data: action.data,
        loading: false,
      };
    case FAILURE_GUIDE:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
