import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  CardMedia,
  Typography,
  CardContent,
  CardActionArea,
  Chip,
  Grid,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../../_store/store.config";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { ILesson } from "../../../_types/lesson.types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      maxHeight: 280,
      width: "100%",
      margin: theme.spacing(1),
    },
    content: {
      minHeight: 130,
    },
    rightChip: {
      position: "absolute",
      cursor: "pointer",
      margin: theme.spacing(1),
      backgroundColor: "green",
    },
  })
);

type Props = {
  courseItem: ILesson;
  type: string;
  path: string;
};

export const CourseItem: React.FC<Props> = ({ type, courseItem, path }) => {
  const classes = useStyles();
  const { title, id, featured_img, info } = courseItem;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    const typer = type === "Exercise" ? "exercise" : "lesson";
    store.dispatch(push(path + "/" + typer + "=" + id));
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={clickHandler}>
        <Grid container>
          <Grid item lg={4} xs={4} sm={4} xl={4}>
            <Chip label={info.grade.name} className={classes.rightChip} />
            <CardMedia
              component="img"
              alt={title}
              height="200"
              image={thumbnailsFinder(featured_img)}
              title={title}
            />
          </Grid>
          <Grid item lg={8} xs={8} sm={8} xl={8}>
            <CardContent className={classes.content}>
              <Typography gutterBottom variant="body1" component="h6">
                {title}
              </Typography>
            </CardContent>
          </Grid>
        </Grid>
      </CardActionArea>
    </Card>
  );
};
