import React from "react";
import * as Yup from "yup";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { Field, Form, Formik } from "formik";
import { TextField } from "formik-material-ui";
import { Link as RouterLink } from "react-router-dom";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import {
  Avatar,
  CssBaseline,
  Box,
  Button,
  Container,
  Link,
  Grid,
  Typography,
  LinearProgress,
} from "@material-ui/core";

import { AppState } from "../../_types";
import { AppActions } from "../../_types";
import { IUser } from "../../_types/user.types";
import { Captcha } from "../../assets/captcha/Captcha";
import { loginRequest, logoutRequest } from "../../_actions/user.actions";
import { push } from "connected-react-router";
import { Path } from "history";

interface LoginPageProps {
  loading: boolean;
  loggedIn: boolean;
}

type Props = LoginPageProps & LinkDispatchProps;

class LoginPage extends React.Component<Props> {
  componentDidMount() {
    if (this.props.loggedIn) {
      this.props.push("/profile");
    }
  }
  render() {
    const { loading, loggedIn } = this.props;
    return !loggedIn ? (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <Formik
            initialValues={{
              username: "",
              password: "",
              recaptcha: "",
            }}
            validationSchema={Yup.object().shape({
              username: Yup.string()
                .max(255)
                .required("Email or Username is required"),
              password: Yup.string().max(255).required("Password is required"),
              recaptcha: Yup.boolean().required("ReCAPTCHA is required"),
            })}
            onSubmit={(event: IUser) => {
              if (event.username && event.password) {
                this.props.login({
                  username: event.username,
                  password: event.password,
                });
              }
            }}
          >
            <Form>
              <Box mb={3}>
                <Typography
                  color="textSecondary"
                  align="center"
                  gutterBottom
                  variant="body2"
                >
                  Sign in on the internal platform
                </Typography>
              </Box>
              <Grid item>
                <Button
                  fullWidth
                  size="large"
                  variant="contained"
                  type="submit"
                  disabled={true}
                >
                  Login with Google
                </Button>
              </Grid>
              <Box mt={3} mb={1}>
                <Typography
                  align="center"
                  color="textSecondary"
                  variant="body1"
                >
                  or login with username / email address
                </Typography>
              </Box>
              <Box my={1}>
                <Field
                  component={TextField}
                  color="secondary"
                  fullWidth
                  label="Email Address/Username"
                  margin="normal"
                  name="username"
                  id="username"
                  placeholder="John"
                  type="username"
                  variant="outlined"
                />
              </Box>
              <Box my={1}>
                <Field
                  component={TextField}
                  color="secondary"
                  fullWidth
                  label="Password"
                  margin="normal"
                  name="password"
                  type="password"
                  variant="outlined"
                />
              </Box>
              <Box my={1}>
                <Field
                  component={Captcha}
                  color="secondary"
                  fullWidth
                  label="Recaptcha"
                  margin="normal"
                  name="recaptcha"
                  type="recaptcha"
                  variant="outlined"
                />
              </Box>
              {loading && <LinearProgress color="secondary" />}
              <Box my={1}>
                <Button
                  color="secondary"
                  disabled={loading}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                >
                  Sign in
                </Button>
              </Box>
              <Grid container style={{ textAlign: "initial" }}>
                <Grid item xs>
                  <Link
                    color="secondary"
                    component={RouterLink}
                    to="/lostpassword"
                    variant="body2"
                  >
                    Forgot password?
                  </Link>
                </Grid>
                <Grid item>
                  <Typography color="textSecondary" variant="body2">
                    Don&apos;t have an account?
                    <Link
                      color="secondary"
                      component={RouterLink}
                      to="/register"
                      variant="body2"
                    >
                      Sign up
                    </Link>
                  </Typography>
                </Grid>
              </Grid>
            </Form>
          </Formik>
        </div>
      </Container>
    ) : null;
  }
}

interface LinkDispatchProps {
  login: (user: IUser) => void;
  logout: () => void;
  push: (path: Path) => void;
}

const mapStateToProps = (state: AppState): LoginPageProps => {
  const { loggedIn, loading } = state.auth;
  return {
    loggedIn,
    loading,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  login: bindActionCreators(loginRequest, dispatch),
  logout: bindActionCreators(logoutRequest, dispatch),
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
