export const REQUEST_FAVORITES = "REQUEST_FAVORITES";
export const SUCCESS_FAVORITES = "SUCCESS_FAVORITES";
export const FAILURE_FAVORITES = "FAILURE_FAVORITES";

export interface DefaultFavoritesState {
  data: IFavorite[];
  error: string;
  loading: boolean;
  nextLoad: number;
}

export interface IFavorite {
  id: number;
  title: string;
  content: string;
  author: string;
  excerpt: string;
  featured_img: string;
  slug: string;
  info: FavoriteInfo;
}
type FavoriteInfo = {
  series: string[];
  instrument: string;
  grade: string[];
  genre: string[];
  img: string[];
  tags: string[];
};

export interface RequestFavorites {
  type: typeof REQUEST_FAVORITES;
}
export interface SeccessFavorites {
  type: typeof SUCCESS_FAVORITES;
  payload: {
    data: IFavorite[];
    nextLoad: number;
  }
}
export interface FailureFavorites {
  type: typeof FAILURE_FAVORITES;
  error: string;
}

export type FavoriteActionTypes =
  | RequestFavorites
  | SeccessFavorites
  | FailureFavorites;
