import {
  SAVE_EXERCISES,
  REQUEST_EXERCISE,
  SUCCESS_EXERCISE,
  FAILURE_EXERCISE,
  REQUEST_EXERCISES,
  SUCCESS_EXERCISES,
  FAILURE_EXERCISES,
  ExerciseActionTypes,
  DefaultExerciseState,
  DefaultExercisesState,
} from "../_types/exercise.types";

const exercisesReducerDefaultState: DefaultExercisesState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const exercisesReducer = (
  state = exercisesReducerDefaultState,
  action: ExerciseActionTypes
): DefaultExercisesState => {
  switch (action.type) {
    case REQUEST_EXERCISES:
      return state;
    case SUCCESS_EXERCISES:
      return {
        ...state,
        data: action.payload.data,
        nextLoad: action.payload.nextLoad,
        loading: false,
      };
    case SAVE_EXERCISES:
      let match = false;
      if (state.data.length)
        match = action.data.every(item => state.data.map(_ => _.id === item.id));
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE_EXERCISES:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const exerciseReducerDefaultState: DefaultExerciseState = {
  loading: true,
  error: "Not Found",
};

export const exerciseReducer = (
  state = exerciseReducerDefaultState,
  action: ExerciseActionTypes
): DefaultExerciseState => {
  switch (action.type) {
    case REQUEST_EXERCISE:
      return { ...state, loading: true, data: undefined };
    case SUCCESS_EXERCISE:
      if (action.data === undefined) {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      } else {
        return {
          ...state,
          data: action.data,
          loading: false,
        };;
      }
    case FAILURE_EXERCISE:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
