import React from "react";
import { SwitchProps } from "formik-material-ui";
import { localTheme } from "../../_helpers/constants";
import ReCaptcha from "./ReCaptcha";

export function Captcha(props: SwitchProps) {
  const {
    form: { setFieldValue, errors, touched },
    field: { name },
  } = props;

  const onChange = React.useCallback(
    (event) => {
      setFieldValue(name, event ? true : false);
    },
    [setFieldValue, name]
  );
  return (
    <ReCaptcha
      action="login"
      theme={localTheme("get") ? "dark" : "light"}
      onChange={onChange}
      onError={touched.recaptcha && errors.recaptcha}
    />
  );
}
