import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../_types";
import { IPost } from "../../_types/post.types";
import { AppState } from "../../_types";
import { requestAllPost } from "../../_actions/post.actions";
import { PostCard } from "./PostCard";
import {
  Chip,
  CircularProgress,
  createStyles,
  Grid,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      padding: theme.spacing(2, 1, 0, 2),
    },
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 143px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: theme.spacing(1),
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface PostsPorps extends WithStyles<typeof useStyles> {
  data: IPost[];
  error: string;
  loading: boolean;
}

type Props = PostsPorps & LinkDispatchProps;

export class PostList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllPost();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <div className={classes.root}>
        <div className={classes.header}>
          <Typography variant="h3"> </Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={": " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <CircularProgress
              color="secondary"
              className={classes.autoMargin}
            />
          ) : data.length ? (
            data.map((post: IPost) => <PostCard key={post.id} post={post} />)
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </div>
    );
  }
}

interface LinkDispatchProps {
  requestAllPost: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): PostsPorps => ({
  classes: ownProps.classes,
  ...state.bank.posts,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllPost: bindActionCreators(requestAllPost, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(PostList));
