import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import {
  CircularProgress,
  Container,
  createStyles,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";

import { AppActions, AppState } from "../../../_types";
import { ILesson } from "../../../_types/lesson.types";
import { PathFinder, pathFinder } from "../../../_helpers/constants";
import { requestExercise } from "../../../_actions/exercise.actions";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "99vw",
      minHeight: "calc(100vh - 128px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 112px)",
        padding: theme.spacing(1),
      },
      padding: theme.spacing(2),
      display: "flex",
      flexDirection: "column",
    },
    autoMargin: {
      margin: "auto",
    },
    content: {
      textAlign: "center",
      width: "100%",
      padding: 10,
      backgroundColor: theme.palette.background.paper,
      border: `1px solid ${theme.palette.secondary.main}`,
    },
  });

interface ExerciseProps extends WithStyles<typeof useStyles> {
  data?: ILesson;
  error: string;
  loading: boolean;
  pathname: string;
}

type Props = ExerciseProps & LinkDispatchProps;

class Exercise extends Component<Props> {
  componentDidMount() {
    const path = pathFinder(this.props.pathname);
    if (path.exercise) this.props.requestExercise(path);
  }

  render() {
    const { classes, data, loading, error } = this.props;
    return (
      <Container className={classes.root}>
        {loading ? (
          <CircularProgress color="secondary" className={classes.autoMargin} />
        ) : data ? (
          <div
            className={classes.content}
            dangerouslySetInnerHTML={{ __html: data.content }}
          />
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestExercise: (path: PathFinder) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): ExerciseProps => ({
  pathname: state.router.location.pathname,
  classes: ownProps.classes,
  ...state.single.exercise,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestExercise: bindActionCreators(requestExercise, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Exercise));
