export const REQUEST_LESSONS = "REQUEST_LESSONS";
export const SUCCESS_LESSONS = "SUCCESS_LESSONS";
export const FAILURE_LESSONS = "FAILURE_LESSONS";

export const SAVE_LESSONS = "SAVE_LESSONS";

export const REQUEST_LESSON = "REQUEST_LESSON";
export const SUCCESS_LESSON = "SUCCESS_LESSON";
export const FAILURE_LESSON = "FAILURE_LESSON";

export interface DefaultLessonsState {
  data: ILesson[];
  error: string;
  loading: boolean;
  nextLoad: number;
}
export interface DefaultLessonState {
  data?: ILesson;
  error: string;
  loading: boolean;
}

export interface ILesson {
  id: number;
  title: string;
  content: string;
  author: string;
  excerpt: string;
  featured_img: string;
  slug: string;
  info: LessonInfo;
}
export type LessonInfo = {
  series: infoDetails;
  instrument: infoDetails;
  grade: infoDetails;
  genre: infoDetails;
};

type infoDetails = {
  name: string,
  id: number,
  img: string,
  color: string,
  description: string
}

export interface RequestLessons {
  type: typeof REQUEST_LESSONS;
}
export interface SeccessLessons {
  type: typeof SUCCESS_LESSONS;
  payload: {
    data: ILesson[];
    nextLoad: number;
  }
}
export interface FailureLessons {
  type: typeof FAILURE_LESSONS;
  error: string;
}

export interface SaveLessons {
  type: typeof SAVE_LESSONS;
  data: ILesson[];
}

export interface RequestLesson {
  type: typeof REQUEST_LESSON;
}
export interface SeccessLesson {
  type: typeof SUCCESS_LESSON;
  data: ILesson;
}
export interface FailureLesson {
  type: typeof FAILURE_LESSON;
  error: string;
}


export type LessonActionTypes =
  | RequestLessons
  | SeccessLessons
  | FailureLessons
  | RequestLesson
  | SeccessLesson
  | FailureLesson
  | SaveLessons;
