import {
  REQUEST_POST,
  SUCCESS_POST,
  FAILURE_POST,
  REQUEST_POSTS,
  SUCCESS_POSTS,
  FAILURE_POSTS,
  PostActionTypes,
  DefaultPostState,
  DefaultPostsState,
} from "../_types/post.types";

const postsReducerDefaultState: DefaultPostsState = {
  data: [],
  loading: true,
  error: 'Not Found',
  nextLoad: 0,
};

export const postsReducer = (
  state = postsReducerDefaultState,
  action: PostActionTypes
): DefaultPostsState => {
  switch (action.type) {
    case REQUEST_POSTS:
      return state;
    case SUCCESS_POSTS:
      return {
        ...state,
        data: action.payload.data,
        nextLoad: action.payload.nextLoad,
        loading: false,
      };
    case FAILURE_POSTS:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const postReducerDefaultState: DefaultPostState = {
  loading: true,
  error: 'Not Found'
};

export const postReducer = (
  state = postReducerDefaultState,
  action: PostActionTypes
): DefaultPostState => {
  switch (action.type) {
    case REQUEST_POST:
      return { ...state, loading: true, data: undefined };
    case SUCCESS_POST:
      if (action.data === undefined) {
        return {
          ...state,
          data: action.data,
          loading: false,
        }
      } else {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      }
    case FAILURE_POST:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
