import { ILesson } from "./lesson.types";

export const REQUEST_EXERCISES = "REQUEST_EXERCISES";
export const SUCCESS_EXERCISES = "SUCCESS_EXERCISES";
export const FAILURE_EXERCISES = "FAILURE_EXERCISES";

export const SAVE_EXERCISES = "SAVE_EXERCISES";

export const REQUEST_EXERCISE = "REQUEST_EXERCISE";
export const SUCCESS_EXERCISE = "SUCCESS_EXERCISE";
export const FAILURE_EXERCISE = "FAILURE_EXERCISE";

export interface DefaultExercisesState {
  data: ILesson[];
  error: string;
  loading: boolean;
  nextLoad: number;
}
export interface DefaultExerciseState {
  data?: ILesson;
  error: string;
  loading: boolean;
}

export interface RequestExercises {
  type: typeof REQUEST_EXERCISES;
}
export interface SeccessExercises {
  type: typeof SUCCESS_EXERCISES;
  payload: {
    data: ILesson[];
    nextLoad: number;
  }
}

export interface SaveExercises {
  type: typeof SAVE_EXERCISES;
  data: ILesson[];
}

export interface FailureExercises {
  type: typeof FAILURE_EXERCISES;
  error: string;
}

export interface RequestExercise {
  type: typeof REQUEST_EXERCISE;
}
export interface SeccessExercise {
  type: typeof SUCCESS_EXERCISE;
  data: ILesson;
  error?: string;
}
export interface FailureExercise {
  type: typeof FAILURE_EXERCISE;
  error: string;
}

export type ExerciseActionTypes =
  | RequestExercises
  | SeccessExercises
  | FailureExercises
  | RequestExercise
  | SeccessExercise
  | FailureExercise
  | SaveExercises;
