export const REQUEST_POSTS = "REQUEST_POSTS";
export const SUCCESS_POSTS = "SUCCESS_POSTS";
export const FAILURE_POSTS = "FAILURE_POSTS";

export const REQUEST_POST = "REQUEST_POST";
export const SUCCESS_POST = "SUCCESS_POST";
export const FAILURE_POST = "FAILURE_POST";

export interface DefaultPostsState {
  data: IPost[];
  error: string;
  loading: boolean;
  nextLoad: number;
}
export interface DefaultPostState {
  data?: IPost;
  error: string;
  loading: boolean;
}

export interface IPost {
  id: number,
  title: string,
  content: string,
  slug: string,
  excerpt: string,
  date: string,
  author: string,
  featured_img: string,
  categories: string[]
  tags: string[]
}

export interface RequestPosts {
  type: typeof REQUEST_POSTS;
}
export interface SeccessPosts {
  type: typeof SUCCESS_POSTS;
  payload: {
    data: IPost[];
    nextLoad: number;
  }
}
export interface FailurePosts {
  type: typeof FAILURE_POSTS;
  error: string;
}

export interface RequestPost {
  type: typeof REQUEST_POST;
}
export interface SeccessPost {
  type: typeof SUCCESS_POST;
  data: IPost;
}
export interface FailurePost {
  type: typeof FAILURE_POST;
  error: string;
}

export type PostActionTypes =
  | RequestPosts
  | SeccessPosts
  | FailurePosts
  | RequestPost
  | SeccessPost
  | FailurePost;
