import { History } from 'history';
import { combineReducers } from "redux";
import { connectRouter } from 'connected-react-router'

import { authReducer } from "./auth.reducer";
import { profileReducer } from "./profile.reducer";
import { favoriteReducer } from "./favorite.reducer";
import { snackbarReducer } from "./snackbar.reducer";
import { postReducer, postsReducer } from "./post.reducer";
import { guideReducer, guidesReducer } from "./guide.reducer";
import { lessonReducer, lessonsReducer } from "./lesson.reducer";
import { courseReducer, coursesReducer } from "./course.reducer";
import { exerciseReducer, exercisesReducer } from "./exercise.reducer";

const rootReducer = (history: History) => combineReducers({
    router: connectRouter(history),
    single: combineReducers({
        post: postReducer,
        guide: guideReducer,
        course: courseReducer,
        lesson: lessonReducer,
        exercise: exerciseReducer,
    }),
    bank: combineReducers({
        posts: postsReducer,
        guides: guidesReducer,
        courses: coursesReducer,
        lessons: lessonsReducer,
        exercises: exercisesReducer,
    }),
    user: combineReducers({
        profile: profileReducer,
        favorites: favoriteReducer,
    }),
    snackbar: snackbarReducer,
    auth: authReducer,
});

export default rootReducer