import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import {
  CircularProgress,
  Grid,
  createStyles,
  Theme,
  Typography,
  WithStyles,
  withStyles,
  Divider,
  Chip,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
} from "@material-ui/core";
import { AppActions, AppState } from "../../../_types";
import { IGuide } from "../../../_types/guide.types";
import { pathFinder, PathFinder } from "../../../_helpers/constants";
import { requestGuide } from "../../../_actions/guide.actions";
import { CourseItem, ExerciseItem } from "./GuideItems";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "99vw",
      minHeight: "calc(100vh - 128px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 112px)",
        padding: theme.spacing(1),
      },
      padding: theme.spacing(2),
      display: "flex",
      flexDirection: "column",
    },
    autoMargin: {
      margin: "auto",
    },
  });

const season = [
  "",
  "",
  " ",
  " ",
  " ",
  " ",
  " ",
  " ",
  " ",
  " ",
  " ",
  " ",
];

interface GuideProps extends WithStyles<typeof useStyles> {
  data?: IGuide;
  loading: boolean;
  error: string;
  pathname: string;
}

type Props = GuideProps & LinkDispatchProps;

class Guide extends Component<Props> {
  componentDidMount() {
    const path = pathFinder(this.props.pathname);
    if (path.guide) this.props.requestGuide(path);
  }

  render() {
    const { classes, data, loading, error, pathname } = this.props;
    let counter = 1;
    return (
      <Grid container justify="center" className={classes.root}>
        {loading ? (
          <CircularProgress color="secondary" className={classes.autoMargin} />
        ) : data ? (
          data.guide_select.map((guide, k) => (
            <List key={k}>
              {guide.exercise && (
                <>
                  <ListItem>
                    <ListItemAvatar>
                      <Avatar>
                        <Chip label={counter++} color="secondary" />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText
                      primary={season[counter]}
                      secondary={guide.exercise_description}
                    />
                  </ListItem>
                  <Divider variant="inset" component="li" />
                  <Grid container justify="center">
                    {guide.exercise.map((guideItem, i) => (
                      <ExerciseItem
                        key={i}
                        index={i}
                        type={"exercise="}
                        path={pathname}
                        exerciseItem={guideItem}
                      />
                    ))}
                  </Grid>
                </>
              )}
              {guide.course && (
                <>
                  <ListItem>
                    <ListItemAvatar>
                      <Avatar>
                        <Chip label={counter++} color="secondary" />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText
                      primary={season[counter]}
                      secondary={guide.course_description}
                    />
                  </ListItem>
                  <Divider variant="inset" component="li" />
                  <Grid container justify="center">
                    {guide.course?.map((guideItem, i) => (
                      <CourseItem
                        key={i}
                        index={i}
                        type={"course="}
                        path={pathname}
                        courseItem={guideItem}
                      />
                    ))}
                  </Grid>
                </>
              )}
            </List>
          ))
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </Grid>
    );
  }
}

interface LinkDispatchProps {
  requestGuide: (path: PathFinder) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): GuideProps => ({
  pathname: state.router.location.pathname,
  classes: ownProps.classes,
  ...state.single.guide,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestGuide: bindActionCreators(requestGuide, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Guide));
