import { ICourse } from "./course.types";
import { ILesson } from "./lesson.types";

export const REQUEST_GUIDES = "REQUEST_GUIDES";
export const SUCCESS_GUIDES = "SUCCESS_GUIDES";
export const FAILURE_GUIDES = "FAILURE_GUIDES";

export const SAVE_GUIDES = "SAVE_GUIDES";

export const REQUEST_GUIDE = "REQUEST_GUIDE";
export const SUCCESS_GUIDE = "SUCCESS_GUIDE";
export const FAILURE_GUIDE = "FAILURE_GUIDE";

export interface DefaultGuidesState {
    data: IGuide[];
    error: string;
    loading: boolean;
    nextLoad: number;
}
export interface DefaultGuideState {
    data?: IGuide;
    error: string;
    loading: boolean;
}

export interface IGuide {
    title: string;
    id: number;
    slug: string;
    teacher: string;
    description: string;
    instrument_id: number;
    instrument_name: string;
    color: string;
    thumbnails: ThumbnailsInfo;
    guide_select: GuideSelect[];
}
export type GuideSelect = {
    exercise_description: string;
    exercise: ILesson[];
    course_description: string;
    course: ICourse[];
}
type ThumbnailsInfo = {
    thumbnail: string;
    medium: string;
    medium_large: string;
    large: string;
    '1536x1536': string;
    '2048x2048': string;
};

export interface RequestGuides {
    type: typeof REQUEST_GUIDES;
}
export interface SeccessGuides {
    type: typeof SUCCESS_GUIDES;
    payload: {
        data: IGuide[];
        nextLoad: number;
    }
}

export interface SaveGuides {
    type: typeof SAVE_GUIDES;
    data: IGuide[];
}

export interface FailureGuides {
    type: typeof FAILURE_GUIDES;
    error: string;
}

export interface RequestGuide {
    type: typeof REQUEST_GUIDE;
}
export interface SeccessGuide {
    type: typeof SUCCESS_GUIDE;
    data: IGuide;
}
export interface FailureGuide {
    type: typeof FAILURE_GUIDE;
    error: string;
}

export type GuideActionTypes =
    | RequestGuides
    | SeccessGuides
    | FailureGuides
    | RequestGuide
    | SeccessGuide
    | FailureGuide
    | SaveGuides;
