import { red } from "@material-ui/core/colors";
import { faIR } from "@material-ui/core/locale";
import { globalStyle } from "./GlobalTheme";

export const LightTheme = createMuiTheme(
  {
    ...globalStyle,
    palette: {
      error: red,
    },
  },
  faIR
);
