import { ILesson } from "./lesson.types";

export const REQUEST_COURSES = "REQUEST_COURSES";
export const SUCCESS_COURSES = "SUCCESS_COURSES";
export const FAILURE_COURSES = "FAILURE_COURSES";

export const SAVE_COURSES = "SAVE_COURSES";

export const REQUEST_COURSE = "REQUEST_COURSE";
export const SUCCESS_COURSE = "SUCCESS_COURSE";
export const FAILURE_COURSE = "FAILURE_COURSE";

export interface DefaultCoursesState {
    data: ICourse[];
    error: string;
    loading: boolean;
    nextLoad: number;
}

export interface DefaultCourseState {
    data?: ICourse;
    error: string;
    loading: boolean;
}

export interface ICourse {
    title: string;
    id: number;
    slug: string;
    content: string;
    teacher: string;
    description: string;
    instrument_id: number,
    instrument_name: string,
    genre_id: number,
    genre_name: string,
    grade: string,
    color: string,
    thumbnails: ThumbnailsInfo;
    course_select: ILesson[];
}

type ThumbnailsInfo = {
    thumbnail: string;
    medium: string;
    medium_large: string;
    large: string;
    '1536x1536': string;
    '2048x2048': string;
};

export interface RequestCourses {
    type: typeof REQUEST_COURSES;
}
export interface SeccessCourses {
    type: typeof SUCCESS_COURSES;
    payload: {
        data: ICourse[];
        nextLoad: number;
    }
}
export interface FailureCourses {
    type: typeof FAILURE_COURSES;
    error: string;
}

export interface SaveGuides {
    type: typeof SAVE_COURSES;
    data: ICourse[];
}

export interface RequestCourse {
    type: typeof REQUEST_COURSE;
}
export interface SeccessCourse {
    type: typeof SUCCESS_COURSE;
    data: ICourse;
}
export interface FailureCourse {
    type: typeof FAILURE_COURSE;
    error: string;
}

export type CourseActionTypes =
    | RequestCourses
    | SeccessCourses
    | FailureCourses
    | RequestCourse
    | SeccessCourse
    | FailureCourse
    | SaveGuides;
