import Axios from "axios";
import { store } from "../_store/store.config";
import { logoutRequest } from "../_actions/user.actions";

export const authHeader = (): HeadersInit => {
  const localUser = localStorage.getItem("user");
  let user: { token: string } = localUser ? JSON.parse(localUser) : null;
  if (user?.token) {
    return { Authorization: "Bearer " + user.token };
  } else {
    return {};
  }
}

export const API = Axios.create({
  baseURL: "https://panel.melodition.com/wp-json",
  headers: authHeader()
});

API.interceptors.response.use(
  res => {
    return res;
  },
  err => {
    console.log(err)
    if (err.response.data.message === 'Expired Token') {
      store.dispatch(logoutRequest())
    }
    return Promise.reject(err);
  }
)