import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { push, goBack } from "connected-react-router";
import { ArrowForward } from "@material-ui/icons";
import { Theme, makeStyles, createStyles } from "@material-ui/core/styles";
import {
  AppBar,
  Link,
  Paper,
  Toolbar,
  IconButton,
  Divider,
  Breadcrumbs,
} from "@material-ui/core";
import {
  AllInbox,
  LibraryMusic,
  QueueMusic,
  MusicNote,
  EmojiSymbols,
  Favorite,
} from "@material-ui/icons";
import { AppActions, AppState } from "../../_types";
import { pathFinder } from "../../_helpers/constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    bracrumbs: {
      padding: theme.spacing(1, 2, 0, 2),
      "& > ol > li": {
        padding: theme.spacing(1, 0.5),
      },
    },
    back: {
      margin: theme.spacing(0, 2),
    },
  })
);
const pages: any = {
  post: <AllInbox />,
  guide: <LibraryMusic />,
  course: <QueueMusic />,
  lesson: <MusicNote />,
  exercise: <EmojiSymbols />,
  favorite: <Favorite />,
};

interface IBreadcrumbs {
  pathname: string;
  single: any;
}

type Props = IBreadcrumbs & LinkDispatchProps;

const Breadcrumb = ({ single, pathname, push, back }: Props) => {
  const classes = useStyles();

  const path = pathFinder(pathname);
  const keys = Object.keys(path);
  const values = Object.values(path);
  const trigger = path.length > 0 ? true : false;

  const linkGenerator = (i: number): string => {
    const linkElement = pathname.split("/").slice(0, i + 2);
    return linkElement.join("/");
  };

  const linkName = (i: number): string => {
    const post = single[keys[i - 1]];
    const name = post?.data?.title;
    return name ? name : values[i - 1];
  };

  const handleClick = (
    event: React.MouseEvent<HTMLAnchorElement, MouseEvent>
  ) => {
    event.preventDefault();
    push(event.currentTarget.pathname);
  };

  return trigger ? (
    <AppBar position="static">
      <Paper square>
        <Toolbar disableGutters>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={back}
            className={classes.back}
          >
            <ArrowForward />
          </IconButton>
          <Divider orientation="vertical" flexItem />
          <Breadcrumbs aria-label="breadcrumb" className={classes.bracrumbs}>
            {keys.map((key, i) => (
              <Link
                key={i}
                href={linkGenerator(i)}
                color="inherit"
                aria-label={linkName(i)}
                onClick={handleClick}
              >
                {i === 0 ? pages[key] : linkName(i)}
              </Link>
            ))}
          </Breadcrumbs>
        </Toolbar>
      </Paper>
    </AppBar>
  ) : null;
};

interface LinkDispatchProps {
  push: (key: string) => void;
  back: () => void;
}

const mapStateToProps = (state: AppState): IBreadcrumbs => {
  const { pathname } = state.router.location;
  return {
    pathname,
    single: state.single,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  push: bindActionCreators(push, dispatch),
  back: bindActionCreators(goBack, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(Breadcrumb);
