import { useEffect } from "react";
import { connect } from "react-redux";
import { useSnackbar } from "notistack";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { AppState, AppActions } from "../../_types";
import { ISnack } from "../../_types/snackbar.types";
import { snackbarRemove } from "../../_actions/snackbar.actions";

let displayed: string[] = [];

interface NotifierProps {
  snackbars?: ISnack[];
}

type Props = NotifierProps & LinkDispatchProps;

const Notifier = ({ snackbars, removeSnack }: Props): null => {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  const storeDisplayed = (id: string) => {
    displayed = [...displayed, id];
  };

  const removeDisplayed = (id: string) => {
    displayed = [...displayed.filter((key) => id !== key)];
  };

  useEffect(() => {
    snackbars?.forEach(
      ({ key, message, options = {}, dismissed = false }: ISnack) => {
        if (dismissed) {
          closeSnackbar(key);
          return;
        }
        if (displayed.includes(key)) return;
        enqueueSnackbar(message, {
          key,
          ...options,
          onClose: (event, reason, myKey) => {
            if (options.onClose) {
              options.onClose(event, reason, myKey);
            }
          },
          onExited: (event, myKey) => {
            if (removeSnack) removeSnack(String(myKey));
            removeDisplayed(String(myKey));
          },
        });
        storeDisplayed(key);
      }
    );
  }, [snackbars, closeSnackbar, enqueueSnackbar, removeSnack]);

  return null;
};
interface LinkDispatchProps {
  removeSnack?: (key: string) => void;
}

const mapStateToProps = (state: AppState): NotifierProps => {
  const { snackbar } = state;
  return {
    snackbars: snackbar.snackbars,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  removeSnack: bindActionCreators(snackbarRemove, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(Notifier);
