import {
  SAVE_LESSONS,
  REQUEST_LESSON,
  SUCCESS_LESSON,
  FAILURE_LESSON,
  REQUEST_LESSONS,
  SUCCESS_LESSONS,
  FAILURE_LESSONS,
  LessonActionTypes,
  DefaultLessonState,
  DefaultLessonsState,
} from "../_types/lesson.types";

const lessonsReducerDefaultState: DefaultLessonsState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const lessonsReducer = (
  state = lessonsReducerDefaultState,
  action: LessonActionTypes
): DefaultLessonsState => {
  switch (action.type) {
    case REQUEST_LESSONS:
      return state;
    case SUCCESS_LESSONS:
      return {
        ...state,
        data: action.payload.data,
        nextLoad: action.payload.nextLoad,
        loading: false,
      };
    case SAVE_LESSONS:
      let match = false;
      if (state.data.length)
        match = action.data.every(item => state.data.map(_ => _.id === item.id));
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE_LESSONS:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const lessonReducerDefaultState: DefaultLessonState = {
  loading: true,
  error: "Not Found",
};

export const lessonReducer = (
  state = lessonReducerDefaultState,
  action: LessonActionTypes
): DefaultLessonState => {
  switch (action.type) {
    case REQUEST_LESSON:
      return { ...state, loading: true, data: undefined };
    case SUCCESS_LESSON:
      return {
        ...state,
        data: action.data,
        loading: false,
      };
    case FAILURE_LESSON:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
