import React from "react";
import { Close } from "@material-ui/icons";
import { store } from "../_store/store.config";
import { IconButton, Slide } from "@material-ui/core";
import { TransitionProps } from "@material-ui/core/transitions/transition";
import { UniqId } from "../_helpers/constants";
import {
  ISnack,
  CLEAR_SNACKBAR,
  SHOW_SNACKBAR,
} from "../_types/snackbar.types";
import { VariantType } from "notistack";

export const errorHandler = (err: any, target: any): void => {
  const error: string = err.response?.data.message.toString();
  if (error) {
    store.dispatch({ type: target, error });
    store.dispatch({
      type: SHOW_SNACKBAR,
      snackbar: snackBarMaker(error, "error"),
    });
  }
};
export const seccessHandler = (err: any, target: any): void => {
  const error: string = err.response.data.message.toString();
  store.dispatch({ type: target, error });
  store.dispatch({
    type: SHOW_SNACKBAR,
    snackbar: snackBarMaker(error, "success"),
  });
};
export const warningHandler = (err: any, target: any): void => {
  const error: string = err.response.data.message.toString();
  store.dispatch({ type: target, error });
  store.dispatch({
    type: SHOW_SNACKBAR,
    snackbar: snackBarMaker(error, "warning"),
  });
};

export const snackBarMaker = (
  message: string,
  variant: VariantType
): ISnack => {
  return {
    message: message.replace(/<[^>]+>/g, ""),
    key: UniqId(),
    options: {
      TransitionComponent: (props: TransitionProps) => (
        <Slide {...props} direction="left" />
      ),
      anchorOrigin: { horizontal: "left", vertical: "bottom" },
      variant: variant,
      autoHideDuration: variant === "error" ? null : 5000,
      action: (key: string) => (
        <IconButton
          style={{ borderRadius: 5 }}
          color="inherit"
          size="small"
          aria-label="close"
          component="span"
          onClick={() =>
            store.dispatch({
              type: CLEAR_SNACKBAR,
              dismissAll: !key,
              key,
            })
          }
        >
          <Close />
        </IconButton>
      ),
    },
  };
};
