import {
  SAVE_COURSES,
  REQUEST_COURSE,
  SUCCESS_COURSE,
  FAILURE_COURSE,
  REQUEST_COURSES,
  SUCCESS_COURSES,
  FAILURE_COURSES,
  CourseActionTypes,
  DefaultCourseState,
  DefaultCoursesState,
} from "../_types/course.types";

const coursesReducerDefaultState: DefaultCoursesState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const coursesReducer = (
  state = coursesReducerDefaultState,
  action: CourseActionTypes
): DefaultCoursesState => {
  switch (action.type) {
    case REQUEST_COURSES:
      return state;
    case SUCCESS_COURSES:
      return {
        ...state,
        data: action.payload.data,
        nextLoad: action.payload.nextLoad,
        loading: false,
      };
    case SAVE_COURSES:
      let match = false;
      if (state.data.length)
        match = action.data.every(item => state.data.map(_ => _.id === item.id));
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE_COURSES:
      return {
        data: [],
        nextLoad: 0,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};

const courseReducerDefaultState: DefaultCourseState = {
  loading: true,
  error: 'Not Found',
};

export const courseReducer = (
  state = courseReducerDefaultState,
  action: CourseActionTypes
): DefaultCourseState => {
  switch (action.type) {
    case REQUEST_COURSE:
      return { ...state, loading: true, data: undefined };
    case SUCCESS_COURSE:
      if (action.data === undefined) {
        return {
          ...state,
          loading: false,
        }
      } else {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      }
    case FAILURE_COURSE:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
