import {
  REQUEST_FAVORITES,
  SUCCESS_FAVORITES,
  FAILURE_FAVORITES,
  FavoriteActionTypes,
  DefaultFavoritesState,
} from "../_types/favorite.types";

const favoriteReducerDefaultState: DefaultFavoritesState = {
  data: [],
  loading: true,
  error: 'Not Found',
  nextLoad: 0,
};

export const favoriteReducer = (
  state = favoriteReducerDefaultState,
  action: FavoriteActionTypes
): DefaultFavoritesState => {
  switch (action.type) {
    case REQUEST_FAVORITES:
      return state;
    case SUCCESS_FAVORITES:
      return {
        ...state,
        nextLoad: action.payload.nextLoad,
        data: action.payload.data,
        loading: false,
      };
    case FAILURE_FAVORITES:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};
